footer {
    background: var(--color-variant);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}


footer a {
    color: var(--color-white);

}

.footer_logo {
font-size: 2rem;
font-weight: 500;
margin-bottom: 2rem;
display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer_sosials  {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_sosials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer_sosials a:hover {
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-white);
}

.copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);

}

@media screen and (max-width: 1024px) {
    
}

/* ========= MEDIA QUERIS ( SMALL DEVICE ) =========== */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer_sosials {
        margin-bottom: 2.6rem;
    }
}