.experience_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.experience {
    padding-top: 2rem;
}

.experience_container > div {
    background: var(--color-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience_container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: pointer;
}

.experience_container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience_content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience_details {
    display: flex;
    gap: 1rem;
}

.experience_details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}


@media screen and (max-width: 1024px) {
    .experience {
        padding-top: 3rem;
    }
   .experience_container {
    grid-template-columns: 1fr;
   }

   .experience_container > div {
    width: 80%;
    padding: 2rem;
    margin: auto;
   }

   .experience_content {
    padding: 1rem;
   }
}

/* ========= MEDIA QUERIS ( SMALL DEVICE ) =========== */
@media screen and (max-width: 600px) {
    .experience_container {
        width: 80%;
        gap: 1rem;
    }

    .experience_container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}